<template>
	<div class="open">	
		<el-table
	    ref="multipleTable"
	    :data="openData"
	    border
	    height="260"
	    tooltip-effect="dark"
	    style="width: 100%"
	    @selection-change="handleSelectionChange">
	    <el-table-column
	      type="selection"
	      width="55">
	    </el-table-column>
	    <el-table-column
	      label="key"
	      prop="resourceKey"
	      >
	    </el-table-column>
	    <el-table-column
	      prop="resourceValue"
	      label="value"
	      >
	    </el-table-column>
	    <el-table-column
	      prop="resourceDesc"
	      label="description"
	      show-overflow-tooltip>
	    </el-table-column>
	  	</el-table>
	  	<div style="margin-top: 20px">
	  		<el-tooltip class="item" effect="dark" content="localStorage" placement="bottom">
		  		<el-button v-show="isShowSave" @click="saveSelection()"></el-button>	
		  	</el-tooltip>
		  	<el-tooltip class="item" effect="dark" content="localStorage" placement="bottom">
		  		<el-button v-show="isShowDel" @click="delSelection()"></el-button>	
		  	</el-tooltip>
			<el-tooltip class="item" effect="dark" content="" placement="bottom">
		  		<el-button @click="writePage()"></el-button>	
		  	</el-tooltip>
	  	</div>	  	
  </div>
</template>

<script>
	import storage from '../storage'
	export default {
		data(){
			return {
				multipleSelection: [],
				env : process.env.NODE_ENV				
			}
		},
		props : ['openData', 'isShowDel', 'isShowSave'],
		filters: {

		},
		computed: {

		},
		created(){
			this.$notify.info({
	          title: 'notice',
	          message: 'openBridge4.0'
	        });
		},
		methods: {
	      	handleSelectionChange(val) {
	        	this.multipleSelection = val;
	      	},
	      	saveSelection() {
	      		var selection = this.$refs.multipleTable.selection;
	      		if(selection.length > 0) {
	      			//storage
	      			//
	      			storage.get('EV').then(rs => {					
						if(rs == null) {
							//storageset
							storage.set({
								EV: selection
							});
						}else {
							//storage
							var concatArr = selection.concat(rs);
							storage.set({
								EV: this.array_ov(concatArr)
							});									
						}
						this.$message({
				            type: 'success',
				            message: ``
				        });	
					});					
	      		}else {
	      			this.$message({
			            type: 'warning',
			            message: ``
			        });	      			
	      		}
	      	},
	      	delSelection() {
	      		var selection = this.$refs.multipleTable.selection;
	      		if(selection.length > 0) {
	      			//storage
	      			//
	      			storage.get('EV').then(rs => {					
						if(rs == null) {
							//do nothing
						}else {
							//storage
							storage.set({
								EV: this.array_diff(rs, selection)
							});						
						}
						this.$message({
				            type: 'success',
				            message: ``
				        });	
				        const app = this.$root.$children[0];
						app.getLocalStorage();
					});
					
	      		}else {
	      			this.$message({
			            type: 'warning',
			            message: ``
			        });	      			
	      		}
	      	},
	      	writePage() {
				var selection = this.$refs.multipleTable.selection;	
				var that = this;
				console.log(selection)			
				if(this.env == 'production') {
					chrome.tabs.query(
				    {active: true, currentWindow: true},
				    function (tabs) {
				        var port = chrome.tabs.connect(//
				            tabs[0].id,
				            {name: "writePage"}//
				        );				        
				     	port.postMessage({event: "writePage", data : selection});//
				     	port.onMessage.addListener(function (msg) {//
				            if(msg.event== "writeOK"){
			                	that.$message({
						            type: 'success',
						            message: ``
						        });
				            };
				        });
				    });
				}
	      	},
	      	//
	      	array_ov(arr) {
				for(var i=0;i<arr.length;i++) {
				 	for(var j=i+1;j<arr.length;j++) {
				 		if(arr[i].resourceKey == arr[j].resourceKey){
				  			arr.splice(j,1);j--;
						} 
				 	}				  		 
				}
				return arr.sort(function(a,b){return a-b});
			},
			//
			array_diff(a, b) {  
			    for(var i=0;i<b.length;i++)  
			    {  
			      for(var j=0;j<a.length;j++)  
			      {  
			        if(a[j].resourceKey==b[i].resourceKey){  
			          a.splice(j,1);  
			          j=j-1;  
			        }  
			      }  
			    }   
			  return a;  
			}  
		}
	}
</script>

<style lang="less">


</style>



